<template>
  <div id="container">
    <h2>coffee list</h2>
    <div class="in_wrap">
      <div class="content" v-for="(c,i) in coffees" :key="i" @click="detail(i)">
        <h3> {{c.title}}</h3>
        <img :src="c.image" :alt="c.id">
      </div>
    </div>
    <button @click="more" class="more">더보기</button>
  </div>
</template>
<script>
  import axios from 'axios'
  import coffee from '@/assets/coffee'
  export default {
    name: 'product',
    data() {
      return {
        coffees: coffee,
        moreList: 0
      }
    },
    methods: {
      more() {
        axios.get(`https://joonsol.github.io/data/more${this.moreList}.json`)
          .then(result => {
            console.log(result);
            this.coffees.push(result.data)
            this.moreList++
            if (this.moreList == 2) {
              this.moreList = 0
            }
          })
      },
      detail(i) {
        this.$router.push({
          name: 'Detail',
          params: {
            contentId: i
          }
        })
      }
    },
  }
</script>
<style scoped>
  .more {
    border: none;
    color: #fff;
    border-radius: 2rem;
    padding: 0 5rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 4rem;
    height: 4rem;
    cursor: pointer;
    background-color: rgb(107, 51, 160);
  }
</style>


