<template>
  <div id="app">
    <Banner :bannerState="bannerState" @bannerClose="closeBanner"></Banner>
    <Header></Header>
    <router-view />
  </div>
</template>

<script>
  import Banner from './components/common/Banner'
  import Header from './components/Header'
  export default {
    name: 'App',
    data() {
      return {
        bannerState: true
      }
    },
    methods: {
      closeBanner() {
        this.bannerState = false
      }
    },
    components: {
      Header,
      Banner,
    }
  }
</script>

<style>
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Noto Sans KR', sans-serif;
  }

  html,
  body {
    font-size: 62.5%;
  }

  a {
    text-decoration: none;
    color: #2c3e50;
    font-size: 1.4rem;
  }

  #container {
    text-align: center;
    margin: 3rem 20px 0;
  }

  h2 {
    margin-bottom: 20px;
    font-weight: 300;
    font-size: 2.3rem;
    text-transform: uppercase;
    background-color: #d798fc;
    padding: 1rem 0;
    color: #fff
  }

  .in_wrap {
    justify-content: space-around;
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
  }

  img {
    width: 200px;
    height: auto;
  }

  h3 {
    color: gray;
    font-size: 1.6rem;
    margin: 3rem 0 1rem;
    position: relative
  }

  h3:after {
    display: block;
    content: "";
    width: 10%;
    height: .5rem;
    background-color: gainsboro;
    margin: 1rem auto
  }
</style>


